@import url('https://fonts.googleapis.com/css?family=Roboto:300,900');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css');
#root {
  /* background-color:#f5f5f5; */
  background-color: #ffffff;
}

.App {
  display: -ms-flexbox;
  display: flex;
  height: 75%;
  width: auto;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Roboto', sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-direction: column;
      flex-direction: column;
  z-index: 50;
}

.email {
  height: 5%;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  font-size: 0.8em;
  padding: 4px;
  opacity: .9;
}

.container {
  height: 100vh;
}

.title {
  text-align: center;
  z-index: 1000;
  
  text-transform: uppercase;
  border-bottom: 1px solid black;
}

.title h1 {
  font-size: 5rem;
  margin-top: 0;
  margin-bottom: 10px;
  z-index: inherit;
}

.title h3 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.icon-wrap {
  display: -ms-flexbox;
  display: flex;
  padding: 20px;
}

.icon {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  margin: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(255, 255, 255);
  background-color: black;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: 700;
  -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
  box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
  cursor: pointer;
}

.square {
  height: 20%;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
 
}

.square-wrap {
  height: 300px;
  width: 300px;
  position: absolute;
  top: 0;
  right: 0;
}

.square-wrap img {
  height: 100%;
  width: auto;
}
@media screen and (max-width: 968px) {

  .title h1 {
    font-size: 3rem;
    margin-top: 0;
    margin-bottom: 10px;
  }
  
  .title h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  .icon-wrap {
    display: -ms-flexbox;
    display: flex;
  
  }

  .icon {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: rgb(255, 255, 255);
    background-color: black;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: 700;
    -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    cursor: pointer;
  }


  .square-wrap {
    height: 150px;
    width: 150px;
    position: absolute;
    top: 0;
    right: 0;
  }


}
@media screen and (max-width: 468px) {

  .title h1 {
    font-size: 2rem;
    margin-top: 0;
    margin-bottom: 10px;
   
  }
  
  .title h3 {
    font-size: 1rem;
    margin-bottom: 10px;
    
  }
  .icon-wrap {
    display: -ms-flexbox;
    display: flex;
  
  }

  .icon {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 2px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: rgb(255, 255, 255);
    background-color: black;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: 700;
    -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    cursor: pointer;
  }


  .square-wrap {
    height: 150px;
    width: 150px;
    position: absolute;
    top: 0;
    right: 0;
  }

  @media only screen and (orientation: landscape) {
 
    .square-wrap {
      height: 50px;
      width: 50px;
      position: absolute;
      top: 0;
      right: 0;
    }
  
  }


}



